body {
  height: auto;
  background: url("./fondo.png") repeat center center;
}
.espaciado {
  margin-top: 120px;
}
.espaciado2 {
  margin-top: 10px;
}
.espaciado3 {
  padding: 50px;
}
.espaciado3 img {
  margin-left: 30px;
}
.espaciado4 {
  padding-top: 50px;
  padding-bottom: 0;
}
.recuadro-reg-log {
  margin: 120px 0 0 0;
  padding: 10px;
  border-radius: 10px;
  background-color: rgba(255, 255, 255, 0.8);
}
.alineacion {
  padding: 30px;
  text-align: center;
  display: inline-block;
}
.alineacion-left {
  padding-top: 70px;
  text-align: left;
  display: inline-block;
}
.alineacion-left p {
  padding-top: 70px;
  text-align: left;
  display: inline-block;
}
.al
.alineacion img {
  padding-bottom: 10px;
}
.alineacion-right {
  padding: 70px 0px 0px 0px;
  text-align: right;
  display: inline-block;
}
.recuadro {
  width: 60%;
  margin: 100px auto;
  padding: 10px;
  top: 20vh;
  position: relative;
  border-radius: 10px;
  background-color: rgba(255, 255, 255, 0.8);
}
.recuadro-lista {
  margin: 50px auto;
  padding: 10px;
  top: 10vh;
  position: relative;
  border-radius: 10px;
}
.recuadro-nc {
  margin: 10px;
  padding: 20px;
  top: 10vh;
  position: relative;
  border-radius: 10px;
  background-color: rgba(255, 255, 255, 0.8);
}
.estilo-top {
  border-bottom: 10px solid green;
  padding: 30px;
  }
.estilo-footer {
  width: 100%;
  color: #fff;
  font-weight: 700;
  background-color: red;
  padding: 30px;
  margin: 50px 0 0 0;
  bottom: 0;
}
.textos-dash {
  font-size: 16px;
  font-weight: 700;
  color: #fff;
  text-align: center;
  text-decoration: none;
}
.Fondo {
  height: 200vh;
}
Link a {
  text-decoration: none !important;
}
.swal2-styled.swal2-confirm {
  border: 0;
  border-radius: 0.25em;
  background: initial;
  background-color: blue !important;
  color: #fff;
  font-size: 1em;
}
.footer {
  position: fixed;
  left: 0;
  bottom: 0;
  width: 100%;
  padding: 20px;
  font-weight: 600;
  background-color: darkred;
  color: white;
  text-align: center;
}
.footer a {
  color: white;
  text-decoration: none;
}
.footer a:hover {
  color: white;
  text-decoration: none;
}
.cs-logout {
  position: relative;
  width: 100%;
  padding: 20px;
  font-weight: 600;
  color: white;
  top: -10px;
  text-align: right;
  display: inline-block;
  z-index: 10;
}